import {useEffect, useState} from 'react';
import Navbar from '../components/Navbar';
import {connect} from 'react-redux';

import recipeSvg from '../imgs/recipe.svg';

import langJson from '../json/language.json';

import './css/FeaturesPage.css';
import RecipesPage from './Features/RecipesPage';
import {setFeature, setFooterBg} from '../redux/';

function FeaturesPage(props) {
    // const [recipeCards, setRecipeCards] = useState([
    //     {
    //         title: 'AAAAAAAAAAAAAAAA',
    //         desc: 'desc',
    //         recipe: {
    //             ingredients: "",
    //             milk: true,
    //             meat: true,
    //             vegan: false,
    //             oven: true,
    //             frying: false,
    //             instructions: 'blahblah12313fgblah'
    //         },
    //         id: 'adfg'
    //     },
    //     {
    //         title: 'title22',
    //         desc: 'desc',
    //         recipe: {
    //             ingredients: "",
    //             milk: true,
    //             meat: false,
    //             vegan: false,
    //             oven: false,
    //             frying: true,
    //             instructions: 'blahblahblah'
    //         },
    //         id: 'adfg'
    //     }
    // ]);

    const [windowSize, setWindowSize] = useState(window.innerWidth);
    window.addEventListener('resize', () => setWindowSize(window.innerWidth));

    useEffect(() => {
        if(props.feature == 'recipes') {
            props.setFooterBg('#17a2b8');
        } else {
            props.setFooterBg('');
        }
    }, [props.feature])

    return (
        <div className="text-center">
            <Navbar/>
            {props.ready ?
                props.username ?
                    !props.feature ?
                    <div>
                        <div className="card user-select-none" style={{width: '18rem', margin: 'auto'}}>
                            <img className="card-img-top" src={recipeSvg}/>
                            <div className="card-body">
                                <h5 className="card-title">{langJson[props.lang][8]}</h5>
                                <p className="card-text" dir={langJson[props.lang][0]}>{`${langJson[props.lang][9]}!`}</p>
                                <button className="btn btn-primary" onClick={() => [props.setFeature('recipes'), setFooterBg('#17a2b8')]}>{langJson[props.lang][10]}</button>
                            </div>
                        </div>
                    </div>
                    :
                    props.feature == 'recipes' ?
                    <RecipesPage windowSize={windowSize}/>
                    :
                    ''
                :
                <div className="display-5" dir={langJson[props.lang][0]}>{`${langJson[props.lang][11]}!`}</div>
            :
            ''}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        username: state.connection.username,
        ready: state.connection.ready,
        lang: state.general.lang,
        userData: state.connection.userData,
        feature: state.general.feature
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setFeature: val => dispatch(setFeature(val)),
        setFooterBg: val => dispatch(setFooterBg(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(FeaturesPage);