import {SET_USERNAME, SET_READY, SET_DATA} from './types';

const initialState = {
    username: '',
    ready: false,
    userData: [],
    feature: ''
}

const reducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_USERNAME: return {
            ...state,
            username: action.payload
        }
        case SET_READY: return {
            ...state,
            ready: action.payload
        }
        case SET_DATA: return {
            ...state,
            userData: action.payload
        }
        default: return state;
    }
}

export default reducer;