import meatSvg from '../imgs/meat.svg';
import milkSvg from '../imgs/milk.svg';
import broccoliSvg from '../imgs/broccoli.svg';
import ovenSvg from '../imgs/oven.svg';
import fryingPanSvg from '../imgs/frying-pan.svg';

import './css/RecipeCard.css';

function RecipeCard(props) {

    const makeBadges = () => {
        const tempIcons = [];
        if(props.recipe.meat) {
            tempIcons.push(<img style={{width: '16px'}} src={meatSvg}/>);
            tempIcons.push(<br/>);
        }
        if(props.recipe.milk) {
            tempIcons.push(<img style={{width: '16px'}} src={milkSvg}/>);
            tempIcons.push(<br/>);
        }
        if(props.recipe.vegan) {
            tempIcons.push(<img style={{width: '16px'}} src={broccoliSvg}/>);
            tempIcons.push(<br/>);
        }
        if(props.recipe.oven) {
            tempIcons.push(<img style={{width: '16px'}} src={ovenSvg}/>);
            tempIcons.push(<br/>);
        }
        if(props.recipe.frying) {
            tempIcons.push(<img style={{width: '16px'}} src={fryingPanSvg}/>);
            tempIcons.push(<br/>);
        }
        if(tempIcons[0]) {
            tempIcons.pop();
        }
        return tempIcons;
    }

    return (
        <div className="card btn mb-3 recipeCard" onClick={() => props.setCurrentRecipe(props.id)} style={{width: '18rem', height: '7rem', margin: 'auto'}}>
            <div className="position-absolute" style={{left: '0.5rem'}}>
                {makeBadges()}
            </div>
            <div className="position-absolute h-100" style={{right: '0.5rem', top: 0, color: 'transparent', backgroundColor: props.stripe, width: '16px'}}></div>
            <div className="card-body">
                <h5 className="card-title overFlowBase overFlowClamp1">{props.title}</h5>
                <p className="card-text overFlowBase overFlowClamp2">{props.desc}</p>
            </div>
        </div>
    )
}

export default RecipeCard;