import { useEffect, useState } from 'react';
import axios from 'axios';
import Navbar from '../components/Navbar';
import { useHistory } from 'react-router';
import {connect} from 'react-redux';

import langJson from '../json/language.json';

function LoginPage(props) {
    const [userName, setUserName] = useState('');
    const [password, setPassword] = useState('');
    const [msgs, setMsgs] = useState();
    const [status, setStatus] = useState();
    const history = useHistory();
    const [apiCall, setApiCall] = useState(false);

    const postLogin = (username, password) => {
        if(username != '' && password != '' && !apiCall) {
            setApiCall(true);
            axios.post('https://recipesheroku.herokuapp.com/login', {username, password})
            .then(response => response.data)
            .then(json => {
                setApiCall(false);
                if(json.bool) {
                    setStatus('Connected!');
                    setMsgs(json.msg);
                    localStorage.setItem('authToken', json.authToken);
                    history.push('/');
                } else {
                    setStatus('Not connected!');
                    setMsgs(json.msg);
                }
            }).catch(() => setApiCall(false));
        } else {
            setMsgs('Enter username & password');
        }
    }

    useEffect(() => props.username ? history.push('/') : '');

    return (
        <div className="text-center">
            <Navbar/>
            <div className="display-2 my-3 border-bottom">{langJson[props.lang][3]}</div>
            <form onSubmit={e => [e.preventDefault(), postLogin(userName, password)]}>
                <input type="text" name="login" placeholder={langJson[props.lang][5]} onChange={e => setUserName(e.target.value)}/>
                <br/>
                <input type="password" name="login" placeholder={langJson[props.lang][6]} onChange={e => setPassword(e.target.value)}/>
                <br/>
                <button className="btn btn-primary" type="submit" name="login">{langJson[props.lang][3]}</button>
            </form>
            <h5 className="text-danger">{msgs}</h5>
            <h1>{status}</h1>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        username: state.connection.username,
        ready: state.connection.ready,
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);