import {SET_LANGUAGE, SET_FEATURE, SET_FOOTER_BG} from './types';

const initialState = {
    lang: 'en',
    version: 2.1,
    footerBg: ''
}

const reducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_LANGUAGE: return {
            ...state,
            lang: action.payload
        }
        case SET_FEATURE: return {
            ...state,
            feature: action.payload
        }
        case SET_FOOTER_BG: return {
            ...state,
            footerBg: action.payload
        }
        default: return state;
    }
}

export default reducer;