import meatSvg from '../imgs/meat.svg';
import milkSvg from '../imgs/milk.svg';
import broccoliSvg from '../imgs/broccoli.svg';
import ovenSvg from '../imgs/oven.svg';
import fryingPanSvg from '../imgs/frying-pan.svg';
import axios from 'axios';
import {connect} from 'react-redux';
import {setData, setLanguage} from '../redux/';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash, faEdit, faSave } from '@fortawesome/free-solid-svg-icons';
import { useEffect, useState } from 'react';
import whiskSvg from '../imgs/whisk.svg';
import autosize from 'autosize';

import langJson from '../json/language.json';
import './css/Recipe.css';
import './css/RecipeError.css';

function Recipe(props) {
    const [recipeStripe, setRecipeStripe] = useState(props.recipe.stripe);
    const [recipeLang, setRecipeLang] = useState(props.recipe.lang);
    const [editMode, setEditMode] = useState(false);
    const [editTitle, setEditTitle] = useState(props.recipe.title);
    const [editDesc, setEditDesc] = useState(props.recipe.desc);
    const [editIngredients, setEditIngredients] = useState(props.recipe.recipe.ingredients);
    const [editInstructions, setEditInstructions] = useState(props.recipe.recipe.instructions);
    const [editMilk, setEditMilk] = useState(props.recipe.recipe.milk);
    const [editMeat, setEditMeat] = useState(props.recipe.recipe.meat);
    const [editVegan, setEditVegan] = useState(props.recipe.recipe.vegan);
    const [editOven, setEditOven] = useState(props.recipe.recipe.oven);
    const [editFrying, setEditFrying] = useState(props.recipe.recipe.frying);
    const [apiCall, setApiCall] = useState(false);

    const [topImagesSize, setTopImagesSize] = useState('6');

    useEffect(() => {
        if(props.windowSize >= 992) {
            setTopImagesSize('6');
        } else {
            setTopImagesSize('4');
        }
    }, [props.windowSize]);

    const makeBadges = () => {
        const tempIcons = [];
        if(props.recipe.recipe.meat) {
            tempIcons.push(<img  style={{width: `${parseInt(topImagesSize)+2}rem`}} className="container" src={meatSvg}/>);
        }
        if(props.recipe.recipe.milk) {
            tempIcons.push(<img  style={{width: `${parseInt(topImagesSize)+2}rem`}} className="container" src={milkSvg}/>);
        }
        if(props.recipe.recipe.vegan) {
            tempIcons.push(<img  style={{width: `${parseInt(topImagesSize)+2}rem`}} className="container" src={broccoliSvg}/>);
        }
        if(props.recipe.recipe.oven) {
            tempIcons.push(<img style={{width: `${parseInt(topImagesSize)+2}rem`}} className="container" src={ovenSvg}/>);
        }
        if(props.recipe.recipe.frying) {
            tempIcons.push(<img style={{width: `${parseInt(topImagesSize)+2}rem`}} className="container" src={fryingPanSvg}/>);
        }
        return tempIcons;
    }

    const deleteRecipe = authToken => {
        if(!apiCall) {
            setApiCall(true);
            axios.post('https://recipesheroku.herokuapp.com/recipedel', {authToken, recipeId: props.recipe.id})
            .then(response => response.data)
            .then(json => {
                setApiCall(false);
                if(json.bool) {
                    props.setCurrentRecipe('');
                    props.setData(json.data);
                } else {
                    console.log('nopers');
                }
            })
            .catch(() => setApiCall(false));
        }
    }

    const saveRecipe = authToken => {
        if(validateRecipe() && !apiCall) {
            setApiCall(true);
            axios.post('https://recipesheroku.herokuapp.com/recipeedit', {authToken, recipeId: props.recipe.id, data: {
                title: editTitle,
                desc: editDesc,
                stripe: recipeStripe,
                lang: recipeLang,
                recipe: {
                    ingredients: editIngredients,
                    instructions: editInstructions,
                    milk: editMilk,
                    meat: editMeat,
                    vegan: editVegan,
                    oven: editOven,
                    frying: editFrying
                },
                id: props.recipe.id
            }}).then(response => response.data)
            .then(json => {
                setApiCall(false)
                if(json.bool) {
                    props.setData(json.data);
                    props.setCurrentRecipe('');
                }
            })
            .catch(() => setApiCall(false));
        }
    }

    const validateRecipe = () => {
        let valid = true;
        console.log('ins:');
        console.log(editInstructions.length);
        if(editTitle == '' ? true : editTitle.length < 3) {
            valid = false;
            document.getElementById('title').classList.add('inpErr');
            document.getElementById('titleErr').textContent = langJson[props.recipe.lang][34];
        } else {
            document.getElementById('title').classList.remove('inpErr');
            document.getElementById('titleErr').textContent = '';
        }
        if(editDesc == '' ? true : editDesc.length < 3) {
            valid = false;
            document.getElementById('desc').classList.add('inpErr');
            document.getElementById('descErr').textContent = langJson[props.recipe.lang][35];
        } else {
            document.getElementById('desc').classList.remove('inpErr');
            document.getElementById('descErr').textContent = '';
        }
        if(editIngredients == '' ? true : editIngredients.split(`\n`).length < 2 || editInstructions.length < 2) {
            valid = false;
            document.getElementById('ing').classList.add('inpErr');
            document.getElementById('ingErr').textContent = langJson[props.recipe.lang][36];
        } else {
            document.getElementById('ing').classList.remove('inpErr');
            document.getElementById('ingErr').textContent = '';
        }
        if(editInstructions == '' ? true : editInstructions.split(`\n`).length < 2 || editInstructions.length < 2) {
            valid = false;
            document.getElementById('ins').classList.add('inpErr');
            document.getElementById('insErr').textContent = langJson[props.recipe.lang][37];
        } else {
            document.getElementById('ins').classList.remove('inpErr');
            document.getElementById('insErr').textContent = '';
        }
        return valid;
    }

    const setBool = type => {
        switch(type) {
            case 'vegan':
                setEditMilk(false);
                setEditMeat(false);
                setEditVegan(true);
                break;
            case 'milk':
                setEditVegan(false);
                setEditMilk(true);
                break;
            case 'meat':
                setEditVegan(false);
                setEditMeat(true);
                break;
        }
    }

    const checkForChanges = () => {
        if(recipeLang == props.recipe.lang && recipeStripe == props.recipe.stripe && editTitle == props.recipe.title && editDesc == props.recipe.desc && editIngredients == props.recipe.recipe.ingredients && editInstructions == props.recipe.recipe.instructions && editMilk == props.recipe.recipe.milk && editMeat == props.recipe.recipe.meat && editVegan == props.recipe.recipe.vegan && editOven == props.recipe.recipe.oven && editFrying == props.recipe.recipe.frying) {
            setEditMode(false);
        } else if(window.confirm(`${langJson[props.recipe.lang][22]}?`)) {
            setRecipeStripe(props.recipe.stripe);
            setRecipeLang(props.recipe.lang);
            setEditTitle(props.recipe.title);
            setEditDesc(props.recipe.desc);
            setEditIngredients(props.recipe.recipe.ingredients);
            setEditInstructions(props.recipe.recipe.instructions);
            setEditMilk(props.recipe.recipe.milk);
            setEditMeat(props.recipe.recipe.meat);
            setEditVegan(props.recipe.recipe.vegan);
            setEditOven(props.recipe.recipe.oven);
            setEditFrying(props.recipe.recipe.frying);
            setEditMode(false);
        }
    }

    const generateIngredients = () => {
        return props.recipe.recipe.ingredients.split(`\n`).map(val => <li className={`${props.recipe.lang == 'en' ? 'text-left' : 'text-right'}`}>{val == '' ? <br/> : val[0] == '$' ? val.slice(1, val.length) : [<img dir={langJson[props.recipe.lang][0]} src={whiskSvg} style={{transform: `translate(${props.recipe.lang == 'en' ? '-' : ''}150%, 25%)`, width: '16px'}} className="ml-1 position-absolute"/>, val]}</li>);
    }

    const generateInstructions = () => {
        let instNum = 1;
        return props.recipe.recipe.instructions.split(`\n`).map((val, index) => <li className={`${index != 0 ? 'my-3' : 'mb-3'} ${props.recipe.lang == 'en' ? 'text-left' : 'text-right'}`}>{val == '' ? <br/> : val[0] == '$' ? <div className="d-inline font-weight-bold text-danger" style={{padding: '5px 9px', border: '2px dashed black', borderRadius: '50%'}}>{instNum++}</div> : val}</li>);
    }

    const alignTextColor = () => {
        let tempColor = 'text-white';
        switch(recipeStripe) {
            case "White":
            case "Yellow":
            case "Pink":
            case "Orange":
            case "Lime":
            case "Aqua":
            case "White":
            case "Cyan":
                tempColor = '';
        }
        return tempColor;
    }

    useEffect(() => document.getElementById('navbar').scrollIntoView({behavior: 'auto'}), []);

    useEffect(() => autosize(document.querySelectorAll('textarea')));

    useEffect(() => {
        props.setSearchRecipe('');
    }, []);

    return (
        <div style={{backgroundColor: '#17a2b8'}}>
            <div>
                <div className="d-flex justify-content-around container-fluid" style={{backgroundColor: '#17a2b8', border: '2px solid black', borderStyle: 'none none solid none'}}>
                    {makeBadges()}
                </div>
            </div>
            <div className="" style={{right: 0, transform: ''}}>
                {editMode ? 
                [
                <button onClick={() => window.confirm(`${langJson[props.recipe.lang][23]}?`) ? saveRecipe(localStorage.getItem('authToken')) : ''} className="btn m-4"><FontAwesomeIcon icon={faSave} className="recipeFeatureBack" size={`${topImagesSize-2}x`}/></button>,
                <button onClick={checkForChanges} className="btn"><FontAwesomeIcon icon={faEdit} className="recipeFeatureBack" size={`${topImagesSize}x`}/></button>,
                <button onClick={() => window.confirm(`${langJson[props.recipe.lang][24]}?`) ? deleteRecipe(localStorage.getItem('authToken')) : ''} className="btn m-4"><FontAwesomeIcon icon={faTrash} className="recipeFeatureBack" size={`${topImagesSize-2}x`}/></button>
                ]
                :
                props.userMade ?
                <div>
                    <button className="btn" onClick={() => setEditMode(true)}><FontAwesomeIcon className="recipeFeatureBack" icon={faEdit} size={`${topImagesSize}x`}/></button>
                    <hr/>
                </div>
                :
                <br/>
                }
            </div>
            {editMode ?
            <div>
                <div style={{left: '2rem'}}>
                    <div dir={langJson[props.recipe.lang][0]} className={`btn badge ${editMilk ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !editMilk ? setBool('milk') : setEditMilk(false)}>{langJson[props.recipe.lang][15]}?</div>
                    <div dir={langJson[props.recipe.lang][0]} className={`btn badge ${editMeat ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !editMeat ? setBool('meat') : setEditMeat(false)}>{langJson[props.recipe.lang][16]}?</div>
                    <div dir={langJson[props.recipe.lang][0]} className={`btn badge ${editVegan ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !editVegan ? setBool('vegan') : setEditVegan(false)}>{langJson[props.recipe.lang][17]}?</div>
                    <div dir={langJson[props.recipe.lang][0]} className={`btn badge ${editOven ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => setEditOven(!editOven)}>{langJson[props.recipe.lang][18]}?</div>
                    <div dir={langJson[props.recipe.lang][0]} className={`btn badge ${editFrying ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={e => setEditFrying(!editFrying)}>{langJson[props.recipe.lang][19]}?</div>
                </div>
                <div class="input-group my-2 container" style={{width: '21rem'}} dir={`${props.recipe.lang == 'en' ? 'ltr' : 'rtl'}`}>
                    <div class="input-group-prepend">
                        <label class="input-group-text" style={{borderRadius: `${props.recipe.lang == 'en' ? '3px 0 0 3px' : '0 3px 3px 0'}`, width: '6rem'}} for="inputGroupSelect01">{langJson[props.recipe.lang][58]}</label>
                    </div>
                    <select style={{borderRadius: `${props.recipe.lang == 'en' ? '0 3px 3px 0' : '3px 0 0 3px'}`}} dir={`${props.recipe.lang == 'en' ? 'ltr' : 'rtl'}`} onChange={e => setRecipeLang(e.target.value)} class="custom-select" id="inputGroupSelect01">
                        <option value="en" selected={recipeLang == 'en'}>English</option>
                        <option value="he" selected={recipeLang == 'he'}></option>
                    </select>
                </div>
                <div class="dropdown" dir={langJson[props.recipe.lang][0]}>
                    <button class={`btn dropdown-toggle ${alignTextColor()}`} style={{backgroundColor: recipeStripe, width: '20rem'}} type="button" data-toggle="dropdown">{langJson[props.recipe.lang][38]}</button>
                    <ul class="dropdown-menu p-0 text-center w-100">
                        <li onClick={() => setRecipeStripe('transparent')}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][39]}</a></li>
                        <li style={{backgroundColor: langJson.en[40]}} onClick={() => setRecipeStripe(langJson.en[40])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][40]}</a></li>
                        <li style={{backgroundColor: langJson.en[41]}} onClick={() => setRecipeStripe(langJson.en[41])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][41]}</a></li>
                        <li style={{backgroundColor: langJson.en[42]}} onClick={() => setRecipeStripe(langJson.en[42])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][42]}</a></li>
                        <li style={{backgroundColor: langJson.en[43]}} onClick={() => setRecipeStripe(langJson.en[43])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][43]}</a></li>
                        <li style={{backgroundColor: langJson.en[44]}} onClick={() => setRecipeStripe(langJson.en[44])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][44]}</a></li>
                        <li style={{backgroundColor: langJson.en[45]}} onClick={() => setRecipeStripe(langJson.en[45])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][45]}</a></li>
                        <li style={{backgroundColor: langJson.en[46]}} onClick={() => setRecipeStripe(langJson.en[46])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][46]}</a></li>
                        <li style={{backgroundColor: langJson.en[47]}} onClick={() => setRecipeStripe(langJson.en[47])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][47]}</a></li>
                        <li style={{backgroundColor: langJson.en[48]}} onClick={() => setRecipeStripe(langJson.en[48])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][48]}</a></li>
                        <li style={{backgroundColor: langJson.en[49]}} onClick={() => setRecipeStripe(langJson.en[49])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][49]}</a></li>
                        <li style={{backgroundColor: langJson.en[50]}} onClick={() => setRecipeStripe(langJson.en[50])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][50]}</a></li>
                        <li style={{backgroundColor: langJson.en[51]}} onClick={() => setRecipeStripe(langJson.en[51])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][51]}</a></li>
                        <li style={{backgroundColor: langJson.en[52]}} onClick={() => setRecipeStripe(langJson.en[52])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][52]}</a></li>
                        <li style={{backgroundColor: langJson.en[53]}} onClick={() => setRecipeStripe(langJson.en[53])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][53]}</a></li>
                        <li style={{backgroundColor: langJson.en[54]}} onClick={() => setRecipeStripe(langJson.en[54])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][54]}</a></li>
                        <li style={{backgroundColor: langJson.en[55]}} onClick={() => setRecipeStripe(langJson.en[55])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][55]}</a></li>
                        <li style={{backgroundColor: langJson.en[56]}} onClick={() => setRecipeStripe(langJson.en[56])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][56]}</a></li>
                        <li style={{backgroundColor: langJson.en[57]}} onClick={() => setRecipeStripe(langJson.en[57])}><a className="dropdown-item btn colorItem">{langJson[props.recipe.lang][57]}</a></li>
                    </ul>
                </div>
                <div dir={langJson[props.recipe.lang][0]} className="h2">{langJson[props.recipe.lang][20]}:</div>
                <input id="title" dir={langJson[props.recipe.lang][0]} style={{whiteSpace: 'pre-wrap', width: '20rem'}} onChange={e => setEditTitle(e.target.value)} defaultValue={props.recipe.title}/>
                <div id="titleErr" className="text-danger h5"></div>
                <div dir={langJson[props.recipe.lang][0]} className="h3">{langJson[props.recipe.lang][12]}:</div>
                <input id="desc" dir={langJson[props.recipe.lang][0]} style={{whiteSpace: 'pre-wrap', width: '20rem'}} onChange={e => setEditDesc(e.target.value)} defaultValue={props.recipe.desc}/>
                <div id="descErr" className="text-danger h5"></div>
                <div dir={langJson[props.recipe.lang][0]} className="h3">{langJson[props.recipe.lang][13]}:</div>
                <textarea id="ing" dir={langJson[props.recipe.lang][0]} style={{whiteSpace: 'pre-wrap'}} onChange={e => setEditIngredients(e.target.value)} cols="40">{props.recipe.recipe.ingredients}</textarea>
                <div id="ingErr" className="text-danger h5"></div>
                <div dir={langJson[props.recipe.lang][0]} className="h3">{langJson[props.recipe.lang][14]}:</div>
                <textarea id="ins" dir={langJson[props.recipe.lang][0]} style={{whiteSpace: 'pre-wrap'}} onChange={e => setEditInstructions(e.target.value)} cols="40">{props.recipe.recipe.instructions}</textarea>
                <div id="insErr" className="text-danger h5"></div>
            </div>
            :
            <div>
                <div className="d-flex justify-content-between">
                    <div className="mx-2"></div>
                    <div>
                        <div>
                            <div dir={langJson[props.recipe.lang][0]} className={`h3 ${props.recipe.lang == 'en' ? 'text-left' : 'text-right'}`}>{langJson[props.recipe.lang][12]}:</div>
                            <div style={{whiteSpace: 'pre-wrap', padding: '1rem'}} className={`${props.recipe.lang == 'en' ? 'text-left' : 'text-right'}`}>{props.recipe.desc}</div>
                        </div>
                        <div dir={langJson[props.recipe.lang][0]} className={`h3 ${props.recipe.lang == 'en' ? 'text-left' : 'text-right'}`}>{langJson[props.recipe.lang][13]}:</div>
                        <ul dir={langJson[props.recipe.lang][0]} className="list-unstyled" style={{padding: '1rem'}}>
                            {generateIngredients()}
                        </ul>
                        <div dir={langJson[props.recipe.lang][0]} className={`h3 ${props.recipe.lang == 'en' ? 'text-left' : 'text-right'}`}>{langJson[props.recipe.lang][14]}:</div>
                        <ul dir={langJson[props.recipe.lang][0]} className="list-unstyled" style={{padding: '1rem'}}>
                            {generateInstructions()}
                        </ul>
                    </div>
                    <div className="mx-2"></div>
                </div>
            </div>}
        </div>
    )
}

const mapStateToProps = state => {
    return {
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setData: val => dispatch(setData(val)),
        setLanguage: val => dispatch(setLanguage(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Recipe);