import {SET_LANGUAGE, SET_FEATURE, SET_FOOTER_BG} from './types';

export const setLanguage = lang => {
    return {
        type: SET_LANGUAGE,
        payload: lang
    }
}

export const setFeature = feature => {
    return {
        type: SET_FEATURE,
        payload: feature
    }
}

export const setFooterBg = color => {
    return {
        type: SET_FOOTER_BG,
        payload: color
    }
}