import React, { useEffect, useState } from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import {connect} from 'react-redux';
import {setUsername, setReady, setLanguage, setData, setFooterBg} from '../redux/';
import heFlag from '../imgs/he_flag.png';
import enFlag from '../imgs/en_flag.png';
import loadingSvg from '../imgs/loading.svg';

import langJson from '../json/language.json';
import './css/Navbar.css';

function Navbar(props) {
    const [widthX, setWidthX] = useState(window.innerWidth);
    
    const checkToken = authToken => {
        props.setReady(false);
        axios.post('https://recipesheroku.herokuapp.com/check', {authToken})
        .then(response => response.data)
        .then(json => {
            if(json.bool) {
                props.setUsername(json.username);
                props.setData(json.data);
                console.log("data:", json.data);
            } else {
                props.setUsername('');
            }
            props.setReady(true);
        })
        .catch(() => {
            props.setUsername('');
            props.setReady(true);
        });
    }

    const logOut = () => {
        localStorage.removeItem('authToken');
        props.setUsername('');
    }

    const changeLanguage = (language) => {
        localStorage.setItem('options-language', language);
        props.setLanguage(language);
    }

    useEffect(() => {
        props.setFooterBg('');
        checkToken(localStorage.getItem('authToken'));
        if(localStorage.getItem('options-language')) {
            props.setLanguage(localStorage.getItem('options-language'));
        }
        window.addEventListener('resize', () => {
            setWidthX(window.innerWidth);
        });
    }, []);

    return (
        <div>
            <nav id="navbar" className="navbar navbar-expand-lg navbar-light bg-light">
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse justify-content-between" id="navbarNavAltMarkup">
                    <div className="navbar-nav" style={{width: `${widthX >= 992 ? '64px' : ''}`}}>
                        <Link className="nav-item nav-link" to="/">{langJson[props.lang][1]}</Link>
                        <Link className="nav-item nav-link" to="/features">{langJson[props.lang][2]}</Link>
                    </div>
                    
                    <div>
                        {props.ready ?
                            props.username ?
                                <div className="navbar-nav">
                                    <Link className="nav-item nav-link" to="/" onClick={logOut}>{langJson[props.lang][7]}</Link>
                                    <Link className="nav-item nav-link" to="/personal">{langJson[props.lang][33]}</Link>
                                </div>
                                :
                                <div className="navbar-nav">
                                    <Link className="nav-item nav-link" to="/login">{langJson[props.lang][3]}</Link>
                                    <Link className="nav-item nav-link" to="/register">{langJson[props.lang][4]}</Link>
                                </div>
                        :
                        <img src={loadingSvg} style={{height: '64px'}}/>
                        }
                    </div>
                    <img type="button" className="langImg" onClick={() => changeLanguage(props.lang == 'en' ? 'he' : 'en')} src={props.lang == 'en' ? enFlag : heFlag}/>
                </div>
            </nav>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        username: state.connection.username,
        ready: state.connection.ready,
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setUsername: val => dispatch(setUsername(val)),
        setReady: val => dispatch(setReady(val)),
        setLanguage: val => dispatch(setLanguage(val)),
        setData: val => dispatch(setData(val)),
        setFooterBg: val => dispatch(setFooterBg(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);