import {useState, useEffect} from 'react';
import recipeSvg from '../imgs/recipe.svg';
import {connect} from 'react-redux';
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import Recipe from './Recipe';
import RecipeCard from './RecipeCard';
import recipesJson from '../json/recipes.json';

import langJson from '../json/language.json';
import './css/ResizeSvg.css';

function RecipeDb(props) {
    const [searchRecipe, setSearchRecipe] = useState('');

    const generateRecipeCards = () => {
        let tempCards = [];
        let tempUserData = recipesJson;
        tempCards.push(<div className="mt-4"></div>);
        if(searchRecipe.toLocaleLowerCase()) {
            for(let i=0;i<searchRecipe.length;i++) {
                tempUserData = tempUserData.filter(item => item.title.toLowerCase().includes(searchRecipe));
            }
        }
        tempCards.push(tempUserData.map((recipe, index) => <RecipeCard title={recipe.title} desc={recipe.desc} recipe={recipe.recipe} setCurrentRecipe={props.setCurrentRecipe} id={recipe.id} stripe={recipe.stripe}/>));
        return tempCards;
    }

    const [topImagesSize, setTopImagesSize] = useState('7x');

    useEffect(() => {
        if(props.windowSize >= 992) {
            setTopImagesSize('7x');
        } else {
            setTopImagesSize('5x');
        }
    }, [props.windowSize]);

    useEffect(() => {
        props.setSearchRecipe('');
    }, []);

    return (
        <div>
            <div className="container-fluid bg-info d-flex justify-content-between" style={{border: '2px solid black', borderStyle: 'solid none solid none'}}>
                <img className="resizeTopImages user-select-none" src={recipeSvg}/>
                <div className="overFlowBase recipeTitle overFlowClamp4">{props.currentRecipe ? recipesJson.find(val => val.id == props.currentRecipe).title : ''}</div>
                <button className="btn" onClick={() => props.currentRecipe ? props.setCurrentRecipe('') : props.setPageOpen('recipes')}><FontAwesomeIcon className="recipeFeatureBack" size={topImagesSize} icon={faTimes}/></button>
            </div>
            <div>
                {props.currentRecipe ?
                <Recipe setSearchRecipe={setSearchRecipe} setCurrentRecipe={props.setCurrentRecipe} recipe={recipesJson.find(val => val.id == props.currentRecipe)} lang="en"/>
                :
                <div>
                    <input placeholder={`${langJson['en'][59]}...`} className="mt-3 text-center" style={{width: '20rem'}} dir="ltr" onChange={e => setSearchRecipe(e.target.value)}/>
                    <hr/>
                    <div className="row mt-4 mx-3">{generateRecipeCards()}</div>
                </div>
                }
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(RecipeDb);