import {useEffect, useState} from 'react';
import {connect} from 'react-redux';

import RecipeCard from '../../components/RecipeCard';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faBook, faPlus, faTimes} from '@fortawesome/free-solid-svg-icons';
import Recipe from '../../components/Recipe';
import RecipeNew from '../../components/RecipeNew';
import RecipeDb from '../../components/RecipeDb';
import recipeSvg from '../../imgs/recipe.svg';
import {setFeature} from '../../redux';

import langJson from '../../json/language.json';

import './css/ResizeSvg.css';
import '../../components/css/RecipeCard.css';
import ScrollToTop from '../../components/ScrollToTop';

function RecipesPage(props) {
    const [pageOpen, setPageOpen] = useState('recipes');
    const [currentRecipe, setCurrentRecipe] = useState('');
    const [searchRecipe, setSearchRecipe] = useState('');

    const generateRecipeCards = () => {
        let tempCards = [];
        let tempUserData = props.userData.recipes;
        tempCards.push(<div className="mt-4"></div>);
        if(searchRecipe.toLowerCase()) {
            for(let i=0;i<searchRecipe.length;i++) {
                tempUserData = tempUserData.filter(item => item.title.toLowerCase().includes(searchRecipe));
            }
        }
        tempCards.push(tempUserData.map((recipe, index) => <RecipeCard title={recipe.title} desc={recipe.desc} recipe={recipe.recipe} setCurrentRecipe={setCurrentRecipe} id={recipe.id} stripe={recipe.stripe}/>));
        return tempCards;
    }

    const [topImagesSize, setTopImagesSize] = useState('7x');
    useEffect(() => {
        if(props.windowSize >= 992) {
            setTopImagesSize('7x');
        } else {
            setTopImagesSize('5x');
        }
    }, [props.windowSize]);
    
    return (
        <div>
            {pageOpen == 'recipes' ?
                <div>
                    <div className="container-fluid bg-info d-flex justify-content-between" style={{border: '2px solid black', borderStyle: 'solid none solid none'}}>
                        <img className="resizeTopImages user-select-none" src={recipeSvg}/>
                        {currentRecipe ? '' : <button className="btn resizeTopImages"><FontAwesomeIcon onClick={() => setPageOpen('recipesDb')} className="recipeFeatureBack" icon={faBook} size={topImagesSize}/></button>}
                        {currentRecipe ? <div className="container overFlowBase recipeTitle overFlowClamp4">{currentRecipe ? props.userData.recipes.find(val => val.id == currentRecipe).title : ''}</div> : <button className="btn"><FontAwesomeIcon onClick={() => setPageOpen('recipesNew')} className="recipeFeatureBack" icon={faPlus} size={topImagesSize}/></button>}
                        <button className="btn" onClick={() => currentRecipe ? setCurrentRecipe('') : props.setFeature('')}><FontAwesomeIcon className="recipeFeatureBack" size={topImagesSize} icon={faTimes}/></button>
                    </div>
                    {!currentRecipe ?
                    <div>
                        <ScrollToTop/>
                        <input placeholder={`${langJson[props.lang][59]}...`} className="mt-3 text-center" style={{width: '20rem'}} dir={langJson[props.lang][0]} onChange={e => setSearchRecipe(e.target.value)}/>
                        <hr/>
                        <div className="row mt-4 mx-3">{generateRecipeCards()}</div>
                    </div>
                    :
                    <Recipe setSearchRecipe={setSearchRecipe} setCurrentRecipe={setCurrentRecipe} recipe={props.userData.recipes.find(val => val.id == currentRecipe)} windowSize={props.windowSize} userMade={true}/>
                    }
                </div>
                :
                pageOpen == 'recipesNew' ?
                    <RecipeNew setSearchRecipe={setSearchRecipe} setPageOpen={setPageOpen}/>
                :
                pageOpen == 'recipesDb' ?
                    <div>
                        <ScrollToTop/>
                        <RecipeDb setSearchRecipe={setSearchRecipe} setPageOpen={setPageOpen} currentRecipe={currentRecipe} windowSize={props.windowSize} setCurrentRecipe={setCurrentRecipe}/>
                    </div>
                :
                ''}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        userData: state.connection.userData,
        feature: state.general.feature,
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setFeature: val => dispatch(setFeature(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(RecipesPage);