import { useEffect, useState } from "react";
import Navbar from "../components/Navbar";
import {connect} from 'react-redux';
import { useHistory } from "react-router";
import axios from "axios";

import './css/PersonalPage.css';
import langJson from '../json/language.json';

function PersonalPage(props) {
    const history = useHistory();
    const [oldPassword, setOldPassword] = useState();
    const [oldPasswordMessage, setOldPasswordMessage] = useState();
    const [newPassword, setNewPassword] = useState();
    const [newPasswordMessage, setNewPasswordMessage] = useState();
    const [repeatNewPassword, setRepeatNewPassword] = useState();
    const [repeatNewPasswordMessage, setRepeatNewPasswordMessage] = useState();
    const [passwordSuccessMessage, setPasswordSuccessMessage] = useState();
    const [passwordFailMessage, setPasswordFailMessage] = useState();

    useEffect(() => !props.username ? history.push('/') : '');

    const changePassword = authToken => {
        let keepBool = true;
        if(!oldPassword) {
            keepBool = false;
            setOldPasswordMessage(`*${langJson[props.lang][29]}*`);
        } else {
            setOldPasswordMessage('');
        }
        if(!newPassword) {
            keepBool = false;
            setNewPasswordMessage(`*${langJson[props.lang][29]}*`);
        } else {
            setNewPasswordMessage('');
        }
        if(!repeatNewPassword) {
            keepBool = false;
            setRepeatNewPasswordMessage(`*${langJson[props.lang][29]}*`);
        } else if(repeatNewPassword == newPassword) {
            setRepeatNewPasswordMessage('');
        } else {
            keepBool = false;
            setRepeatNewPasswordMessage(`*${langJson[props.lang][30]}*`);
        }
        if(keepBool) {
            console.log(oldPassword);
            axios.post('https://recipesheroku.herokuapp.com/changepass', {authToken, oldPass: oldPassword, newPass: newPassword})
            .then(response => response.data)
            .then(json => {
                if(json.bool) {
                    setOldPasswordMessage('');
                    setNewPasswordMessage('');
                    setRepeatNewPasswordMessage('');
                    setPasswordFailMessage('');
                    setPasswordSuccessMessage(`${langJson[props.lang][31]}!`);
                } else {
                    setPasswordSuccessMessage('');
                    setPasswordFailMessage(`${langJson[props.lang][32]}!`);
                }
            })
        } else {
            setPasswordSuccessMessage('');
            setPasswordFailMessage('');
        }
        setOldPassword('');
        setNewPassword('');
        setRepeatNewPassword('');
    }

    return (
        <div className="text-center">
            <Navbar/>
            <div className="bg-warning">
                <div className="mainPassDiv container">
                    <div dir={langJson[props.lang][0]} className="h1">{`${langJson[props.lang][25]}:`}</div>
                    <div className="secondaryDiv">
                        <div dir={langJson[props.lang][0]} className="passDiv">{`${langJson[props.lang][26]}:`}</div>
                        <input type="password" placeholder={langJson[props.lang][26]} className="passInput" value={oldPassword} onChange={e => setOldPassword(e.target.value)}/>
                        <div className="text-danger" style={{fontSize: '14px'}}>{oldPasswordMessage}</div>
                    </div>
                    <div className="secondaryDiv">                        
                        <div dir={langJson[props.lang][0]} className="passDiv">{`${langJson[props.lang][27]}:`}</div>
                        <input type="password" placeholder={langJson[props.lang][27]} className="passInput" value={newPassword} onChange={e => setNewPassword(e.target.value)}/>
                        <div className="text-danger" style={{fontSize: '14px'}}>{newPasswordMessage}</div>
                    </div>
                    <div className="secondaryDiv">                        
                        <div dir={langJson[props.lang][0]} className="passDiv">{`${langJson[props.lang][28]}:`}</div>
                        <input type="password" placeholder={langJson[props.lang][28]} className="passInput" value={repeatNewPassword} onChange={e => setRepeatNewPassword(e.target.value)}/>
                        <div className="text-danger" style={{fontSize: '14px'}}>{repeatNewPasswordMessage}</div>
                    </div>
                    <button className="btn btn-info" onClick={() => changePassword(localStorage.getItem('authToken'))}>{langJson[props.lang][25]}</button>
                    <div dir={langJson[props.lang][0]} className="text-success h3">{passwordSuccessMessage}</div>
                    <div dir={langJson[props.lang][0]} className="text-danger h3">{passwordFailMessage}</div>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        username: state.connection.username,
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PersonalPage);