import { faPlusCircle, faTimes, faTrash } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useEffect, useState } from "react";
import recipeSvg from '../imgs/recipe.svg';
import axios from "axios";
import {connect} from 'react-redux';
import {setData} from '../redux/';
import autosize from 'autosize';

import langJson from '../json/language.json';

import './css/RecipeError.css';

function RecipeNew(props) {
    const [recipeStripe, setRecipeStripe] = useState('');
    const [recipeLang, setRecipeLang] = useState(props.lang);
    const [recipeTitle, setRecipeTitle] = useState(false);
    const [recipeDesc, setRecipeDesc] = useState(false);
    const [recipeIngredients, setRecipeIngredients] = useState(false);
    const [recipeInstructions, setRecipeInstructions] = useState(false);

    const [milkBool, setMilkBool] = useState(false);
    const [meatBool, setMeatBool] = useState(false);
    const [veganBool, setVeganBool] = useState(false);
    const [ovenBool, setOvenBool] = useState(false);
    const [fryingBool, setFryingBool] = useState(false);
    const [apiCall, setApiCall] = useState(false);

    const setBool = type => {
        switch(type) {
            case 'vegan':
                setMilkBool(false);
                setMeatBool(false);
                setVeganBool(true);
                break;
            case 'milk':
                setVeganBool(false);
                setMilkBool(true);
                break;
            case 'meat':
                setVeganBool(false);
                setMeatBool(true);
                break;
        }
    }

    const saveRecipe = authToken => {
        if(validateRecipe() && !apiCall) {
            setApiCall(true);
            axios.post('https://recipesheroku.herokuapp.com/recipeadd', {authToken, recipe: {
                title: recipeTitle,
                desc: recipeDesc,
                stripe: recipeStripe,
                lang: recipeLang,
                recipe: {
                    ingredients: recipeIngredients,
                    instructions: recipeInstructions,
                    milk: milkBool,
                    meat: meatBool,
                    vegan: veganBool,
                    oven: ovenBool,
                    frying: fryingBool
                }
            }}).then(response => response.data)
            .then(json => {
                setApiCall(false);
                if(json.bool) {
                    props.setData(json.data);
                    props.setPageOpen('recipes');
                }
            })
            .catch(() => setApiCall(false));
        }
    }

    const validateRecipe = () => {
        let valid = true;
        console.log('ins:');
        console.log(recipeInstructions.length);
        if(recipeTitle == '' ? true : recipeTitle.length < 3) {
            valid = false;
            document.getElementById('title').classList.add('inpErr');
            document.getElementById('titleErr').textContent = langJson[props.lang][34];
        } else {
            document.getElementById('title').classList.remove('inpErr');
            document.getElementById('titleErr').textContent = '';
        }
        if(recipeDesc == '' ? true : recipeDesc.length < 3) {
            valid = false;
            document.getElementById('desc').classList.add('inpErr');
            document.getElementById('descErr').textContent = langJson[props.lang][35];
        } else {
            document.getElementById('desc').classList.remove('inpErr');
            document.getElementById('descErr').textContent = '';
        }
        if(recipeIngredients == '' ? true : recipeIngredients.split(`\n`).length < 2 || recipeInstructions.length < 2) {
            valid = false;
            document.getElementById('ing').classList.add('inpErr');
            document.getElementById('ingErr').textContent = langJson[props.lang][36];
        } else {
            document.getElementById('ing').classList.remove('inpErr');
            document.getElementById('ingErr').textContent = '';
        }
        if(recipeInstructions == '' ? true : recipeInstructions.split(`\n`).length < 2 || recipeInstructions.length < 2) {
            valid = false;
            document.getElementById('ins').classList.add('inpErr');
            document.getElementById('insErr').textContent = langJson[props.lang][37];
        } else {
            document.getElementById('ins').classList.remove('inpErr');
            document.getElementById('insErr').textContent = '';
        }
        return valid;
    }

    const alignTextColor = () => {
        let tempColor = 'text-white';
        switch(recipeStripe) {
            case "White":
            case "Yellow":
            case "Pink":
            case "Orange":
            case "Lime":
            case "Aqua":
            case "White":
            case "Cyan":
                tempColor = '';
        }
        return tempColor;
    }

    useEffect(() => autosize(document.querySelectorAll('textarea')));

    useEffect(() => {
        props.setSearchRecipe('');
    }, []);

    return (
        <div style={{backgroundColor: '#17a2b8'}}>
            <div className="container-fluid bg-info d-flex justify-content-between" style={{border: '2px solid black', borderStyle: 'solid none solid none'}}>
                <img className="card-img-top" src={recipeSvg} style={{width: '9rem'}}/>
                <div></div>
                <button className="btn" onClick={() => props.setPageOpen('recipes')}><FontAwesomeIcon className="recipeFeatureBack" size="8x" icon={faTimes}/></button>
            </div>
            <div style={{left: '2rem'}}>
                <div dir={langJson[props.lang][0]} className={`btn badge ${milkBool ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !milkBool ? setBool('milk') : setMilkBool(false)}>{langJson[props.lang][15]}?</div>
                <div dir={langJson[props.lang][0]} className={`btn badge ${meatBool ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !meatBool ? setBool('meat') : setMeatBool(false)}>{langJson[props.lang][16]}?</div>
                <div dir={langJson[props.lang][0]} className={`btn badge ${veganBool ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => !veganBool ? setBool('vegan') : setVeganBool(false)}>{langJson[props.lang][17]}?</div>
                <div dir={langJson[props.lang][0]} className={`btn badge ${ovenBool ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={() => setOvenBool(!ovenBool)}>{langJson[props.lang][18]}?</div>
                <div dir={langJson[props.lang][0]} className={`btn badge ${fryingBool ? 'badge-success' : 'badge-danger'} m-2 p-3`} style={{width: '7rem', borderColor: 'black'}} onClick={e => setFryingBool(!fryingBool)}>{langJson[props.lang][19]}?</div>
            </div>
            <form onSubmit={e => [e.preventDefault(), saveRecipe(localStorage.getItem('authToken'))]}>
                <div class="input-group my-2 container" style={{width: '21rem'}} dir={`${props.lang == 'en' ? 'ltr' : 'rtl'}`}>
                    <div class="input-group-prepend">
                        <label class="input-group-text" style={{borderRadius: `${props.lang == 'en' ? '3px 0 0 3px' : '0 3px 3px 0'}`, width: '6rem'}} for="inputGroupSelect01">{langJson[props.lang][58]}</label>
                    </div>
                    <select style={{borderRadius: `${props.lang == 'en' ? '0 3px 3px 0' : '3px 0 0 3px'}`}} dir={`${props.lang == 'en' ? 'ltr' : 'rtl'}`} onChange={e => setRecipeLang(e.target.value)} class="custom-select" id="inputGroupSelect01">
                        <option value="en" selected={recipeLang == 'en'}>English</option>
                        <option value="he" selected={recipeLang == 'he'}></option>
                    </select>
                </div>
                <div class="dropdown" dir={langJson[props.lang][0]}>
                    <button class={`btn dropdown-toggle ${alignTextColor()}`} style={{backgroundColor: recipeStripe, width: '20rem'}} type="button" data-toggle="dropdown">{langJson[props.lang][38]}</button>
                    <ul class="dropdown-menu p-0 text-center w-100">
                        <li onClick={() => setRecipeStripe('transparent')}><a className="dropdown-item btn colorItem">{langJson[props.lang][39]}</a></li>
                        <li style={{backgroundColor: langJson.en[40]}} onClick={() => setRecipeStripe(langJson.en[40])}><a className="dropdown-item btn colorItem">{langJson[props.lang][40]}</a></li>
                        <li style={{backgroundColor: langJson.en[41]}} onClick={() => setRecipeStripe(langJson.en[41])}><a className="dropdown-item btn colorItem">{langJson[props.lang][41]}</a></li>
                        <li style={{backgroundColor: langJson.en[42]}} onClick={() => setRecipeStripe(langJson.en[42])}><a className="dropdown-item btn colorItem">{langJson[props.lang][42]}</a></li>
                        <li style={{backgroundColor: langJson.en[43]}} onClick={() => setRecipeStripe(langJson.en[43])}><a className="dropdown-item btn colorItem">{langJson[props.lang][43]}</a></li>
                        <li style={{backgroundColor: langJson.en[44]}} onClick={() => setRecipeStripe(langJson.en[44])}><a className="dropdown-item btn colorItem">{langJson[props.lang][44]}</a></li>
                        <li style={{backgroundColor: langJson.en[45]}} onClick={() => setRecipeStripe(langJson.en[45])}><a className="dropdown-item btn colorItem">{langJson[props.lang][45]}</a></li>
                        <li style={{backgroundColor: langJson.en[46]}} onClick={() => setRecipeStripe(langJson.en[46])}><a className="dropdown-item btn colorItem">{langJson[props.lang][46]}</a></li>
                        <li style={{backgroundColor: langJson.en[47]}} onClick={() => setRecipeStripe(langJson.en[47])}><a className="dropdown-item btn colorItem">{langJson[props.lang][47]}</a></li>
                        <li style={{backgroundColor: langJson.en[48]}} onClick={() => setRecipeStripe(langJson.en[48])}><a className="dropdown-item btn colorItem">{langJson[props.lang][48]}</a></li>
                        <li style={{backgroundColor: langJson.en[49]}} onClick={() => setRecipeStripe(langJson.en[49])}><a className="dropdown-item btn colorItem">{langJson[props.lang][49]}</a></li>
                        <li style={{backgroundColor: langJson.en[50]}} onClick={() => setRecipeStripe(langJson.en[50])}><a className="dropdown-item btn colorItem">{langJson[props.lang][50]}</a></li>
                        <li style={{backgroundColor: langJson.en[51]}} onClick={() => setRecipeStripe(langJson.en[51])}><a className="dropdown-item btn colorItem">{langJson[props.lang][51]}</a></li>
                        <li style={{backgroundColor: langJson.en[52]}} onClick={() => setRecipeStripe(langJson.en[52])}><a className="dropdown-item btn colorItem">{langJson[props.lang][52]}</a></li>
                        <li style={{backgroundColor: langJson.en[53]}} onClick={() => setRecipeStripe(langJson.en[53])}><a className="dropdown-item btn colorItem">{langJson[props.lang][53]}</a></li>
                        <li style={{backgroundColor: langJson.en[54]}} onClick={() => setRecipeStripe(langJson.en[54])}><a className="dropdown-item btn colorItem">{langJson[props.lang][54]}</a></li>
                        <li style={{backgroundColor: langJson.en[55]}} onClick={() => setRecipeStripe(langJson.en[55])}><a className="dropdown-item btn colorItem">{langJson[props.lang][55]}</a></li>
                        <li style={{backgroundColor: langJson.en[56]}} onClick={() => setRecipeStripe(langJson.en[56])}><a className="dropdown-item btn colorItem">{langJson[props.lang][56]}</a></li>
                        <li style={{backgroundColor: langJson.en[57]}} onClick={() => setRecipeStripe(langJson.en[57])}><a className="dropdown-item btn colorItem">{langJson[props.lang][57]}</a></li>
                    </ul>
                </div>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][20]}:</div>
                <input id="title" dir={langJson[props.lang][0]} onChange={e => setRecipeTitle(e.target.value)} style={{width: '20rem'}}/>
                <div id="titleErr" className="text-danger h5"></div>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][12]}:</div>
                <input id="desc" dir={langJson[props.lang][0]} onChange={e => setRecipeDesc(e.target.value)} style={{width: '20rem'}}/>
                <div id="descErr" className="text-danger h5"></div>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][13]}:</div>
                <textarea id="ing" dir={langJson[props.lang][0]} onChange={e => setRecipeIngredients(e.target.value)} cols="40"></textarea>
                <div id="ingErr" className="text-danger h5"></div>
                <div dir={langJson[props.lang][0]} className="h3">{langJson[props.lang][14]}:</div>
                <textarea id="ins" dir={langJson[props.lang][0]} onChange={e => setRecipeInstructions(e.target.value)} cols="40"></textarea>
                <div id="insErr" className="text-danger h5"></div>
                <br/>
                <button type="submit" className="btn btn-primary my-2 px-4">{langJson[props.lang][21]}</button>
            </form>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        username: state.connection.username,
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setData: val => dispatch(setData(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(RecipeNew);