import {connect} from 'react-redux';

function Footer(props) {
    const styles =  {
        color: 'white',
        paddingTop: '1rem',
        position: 'relative',
        bottom: 0,
        width: '100%',
        textAlign: 'center',
        backgroundColor: props.footerBg ? props.footerBg : ''
    }  
    return (
        <div style={styles}>
            <div className="container">
                <a href="https://saharzzz.github.io/saharhayon/" className={`${props.class} link-unstyled text-decoration-none`}>{` ${new Date().getFullYear()} Copyright: Sahar Hayon`}</a>
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        footerBg: state.general.footerBg
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Footer);