import {useState, useEffect} from 'react';
import langJson from '../json/language.json';
import {connect} from 'react-redux';

function ScrollToTop(props) {
    const [scrollY, setScrollY] = useState(0);

    useEffect(() => {
        document.querySelector('ion-content').addEventListener('ionScroll', e => setScrollY(e.detail.currentY));
    }, []);

    const scrollToTop = () => {
        document.getElementById('navbar').scrollIntoView({behavior: 'smooth'});
    }
    return (
        <div className="w-100 position-fixed btn" onClick={scrollToTop} style={{zIndex: 4, left: '50%', transform: 'translateX(-50%)', backgroundColor: '#f6f6f6', opacity: 0.75, height: '50px', display: scrollY > 280 ? 'block' : 'none', fontSize: '30px', top: 0}}>{langJson[props.lang][60]}</div>
    )
}

const mapStateToProps = state => {
    return {
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ScrollToTop);