import {useState} from 'react';
import axios from 'axios';
import Navbar from '../components/Navbar';
import {connect} from 'react-redux';

function MainPage(props) {
    const [apiCall, setApiCall] = useState(false);

    const checkToken = authToken => {
        if(!apiCall) {
            setApiCall(true);
            axios.post('https://recipesheroku.herokuapp.com/check', {authToken})
            .then(response => response.data)
            .then(json => {
                setApiCall(false);
                if(json.bool) {
                    alert(json.username);
                } else {
                    alert('nope');
                }
            }).catch(() => setApiCall(false));
        }
    }

    return (
        <div className="text-center">
            <Navbar/>
            <p>{`Welcome ${props.username ? props.username : 'Guest'}!`}</p>
            <button className="btn btn-info" onClick={() => checkToken(localStorage.getItem('authToken'))}>Test auth</button>
            <div className="h2 font-weight-bold position-absolute border-top border-right m-0 user-select-none" style={{bottom: 0}}>V-{props.version}</div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        username: state.connection.username,
        version: state.general.version
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MainPage);