import {SET_USERNAME, SET_READY, SET_DATA} from './types';

export const setUsername = username => {
    return {
        type: SET_USERNAME,
        payload: username
    }
}

export const setReady = bool => {
    return {
        type: SET_READY,
        payload: bool
    }
}

export const setData = data => {
    return {
        type: SET_DATA,
        payload: data
    }
}